import React, {Component} from 'react';
import DeviceProfileHeader from "./DeviceProfileHeader";
import DeviceProfileForm from "./DeviceProfileForm";

class DeviceProfile extends Component {
    onCancelClick = () => {
        this.props.onCancelClicked();
    }

    onSaveClick = (device) => {
        // console.log("in profile", device)
        this.props.onSaveClicked(device);
    }

    renderForm() {
        return <DeviceProfileForm device={this.props.device} onCancelClick={this.onCancelClick}
                                  onSaveClick={this.onSaveClick}/>
    }

    render() {
        // console.log("props in render", this.props.device);
        const device = this.props.device;
        return (
            <div className="container-fluid">
                <div className="profile-form">
                    <div className="row profile-form-title">
                        <h4 className="modal-title text-left text-white mb-3" id="exampleModalCenterTitle">
                            Device Profile
                        </h4>
                        <DeviceProfileHeader {...this.props.device} />
                    </div>
                    <div>
                        {this.renderForm()}
                    </div>

                </div>
            </div>
        );
    }
}

export default DeviceProfile;
