import React from "react";
import DeviceDataTableHeader from "./DeviceDataTableHeader";
import DeviceDataTableBody from "./DeviceDataTableBody";
import DeviceDataTableFooter from "./DeviceDataTableFooter";
import '../../css/DeviceDataTable.css'
import moment from "moment";

class DeviceDataTable extends React.Component {
    state = {
        tableOptions: {
            deviceId: this.props.deviceId,
            fromTimestamp: null,//moment(moment().valueOf() - 43200000),
            page: 0,
            pageSize: 10,
            toTimestamp: null //moment().valueOf()
        },
        pageSize: 10,
        currentPage: 0,
        startClearanceOrder: 0,
        endClearanceOrder: 0,
        deviceIds: []
    }
    onHeaderChange = (options) => {
        this.setState({...this.state.tableOptions, ...options})
    }

    onSearchClick = (options) => {
        let tableOptions = {...this.state.tableOptions, ...options, page: 0}
        this.setState({tableOptions})
        this.props.onSearchClick(tableOptions);
    }

    onPagingClicked = (page) => {
        let tableOptions = {...this.state.tableOptions, page}
        this.setState({tableOptions});
        this.props.onPagingClick(tableOptions);

    }

    onPageSizeChange = (pageSize) => {
        let tableOptions = {...this.state.tableOptions, pageSize, page: 0}
        this.setState({tableOptions});
        this.props.onPageSizeChange(tableOptions);
    }


    renderFooter() {
        if (!this.props.displayFooter) {
            return null;
        }
        console.log("DeviceDataTable renderFooter props", this.props)
        let currentPage = this.props.currentPage;
        let totalPageCount = this.props.totalPageCount;
        let displayPageCount = this.props.displayPageCount;
        return <DeviceDataTableFooter currentPage={currentPage}
                                      totalPage={totalPageCount}
                                      displayPageCount={displayPageCount}
                                      onPagingClicked={this.onPagingClicked}/>

    }

    renderHeader() {
        if (!this.props.displayFilter) {
            return null;
        }
        return <DeviceDataTableHeader options={this.state.tableOptions} onChange={this.onHeaderChange}
                                      onSearchClick={this.onSearchClick} onPageSizeChange={this.onPageSizeChange}/>
    }

    deviceNames() {
        if (this.props.deviceDataList) {
            this.props.deviceDataList.map((device) => {
                return device.deviceId;
            })
        }
        return [];
    }

    render() {
        return (
            <div className="deviceDataTable">
                <div className="deviceDataTableHeader">
                    {this.renderHeader()}
                </div>
                <div className="deviceDataTableBody">
                    <DeviceDataTableBody data={this.props.deviceDataList}/>
                </div>
                <div className="deviceDataTableFooter">
                    {this.renderFooter()}

                </div>
            </div>
        );
    }
}


export default DeviceDataTable;