import React from "react";
import '../../css/dashboard.css';
import ethereumSvg from '../../image/ethereum.svg'
import remoteControlSvg from '../../image/remoteControl.svg'
import clearanceOrderSvg from '../../image/clearanceOrder.svg'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDollarSign } from "@fortawesome/free-solid-svg-icons";


const icon = {
    'device': {
        'btnClass': 'btn btn-primary btn-circle btn-lg',
        'icon': <img width="30" src={remoteControlSvg} alt="device" />
    },
    'txCount': {
        'btnClass': 'btn btn-purple btn-circle btn-lg',
        'icon': <i className="fa-x text-white"> <FontAwesomeIcon icon={faDollarSign} /></i>
    },
    'ethereum': {
        'btnClass': 'btn btn-success btn-circle btn-lg',
        'icon': <img width="15" src={ethereumSvg} alt="ethereum" />
    },
    'clearanceOrder': {
        'btnClass': 'btn btn-danger btn-circle btn-lg',
        'icon': <img width="30" src={clearanceOrderSvg} alt="clearanceOrder" />
    }
}


const etherumStatus = {
    'ETHEREUM_NORMAL': {
        'content': 'Normal',
        'description': <small className='text-success'>Ethereum is in Good Status</small>
    },
    'ETHEREUM_CONGESTION': {
        'content': 'Pending',
        'description': <small className='text-warn'>Ethereum is in Pending Status</small>
    },
    'ETHEREUM_BLOCK': {
        'content': 'Error',
        'description': <small className='text-danger'>Ethereum is in Error Status</small>
    },
    'ETHEREUM_ERROR': {
        'content': 'Error',
        'description': <small className='text-danger'>Ethereum is in Error Status</small>
    }
}

function clearanceOrderStatus(content) {
    if (content < 0 || content === undefined) {
        return ({
            'content': '',
            'description': <small className='text-danger'>Clearance Status Error</small>
        })
    } else {
        return ({
            'content': content,
            'description': <small className='text-success'>Clearance Status Normal</small>
        })
    }
}

function translateContent(type, content) {
    if (type === 'ethereum') {
        if (content !== undefined) {
            return etherumStatus[content];
        } else {
            return ({
                'content': '',
                'description': <small className='text-danger'>Ethereum is in Error Status</small>
            })
        }
    } else if (type === 'clearanceOrder') {
        return clearanceOrderStatus(content);
    } else if (type === 'device') {
        if (content !== undefined) {
            return ({
                'content': content,
                'description': <small className='text-success'>100% Online</small>
            })
        } else {
            return ({
                'content': '',
                'description': <small className='text-danger'>0% Online</small>
            })
        }
    } else if (type === 'txCount') {
        if (content !== undefined) {
            return ({
                'content': content,
                'description': <small className='text-danger'>30% Used<div><p className='text-secondary'>Since last purchased</p></div></small>
            })
        } else {
            return ({
                'content':'',
                'description': <small className='text-danger'>0% Used</small>
            })
        }
    } else {
        return ({
            'content': '',
            'description': ''
        })
    }
}

export default class StatusCard extends React.Component {
    render() {
        const type = this.props.type
        
        const content = translateContent(type, this.props.content)
       
        return (
            <div className="card shadow h-100 py-2">
                <div className="card-body">
                    <div className="row no-gutters align-items-center">
                        <div className="col mr-2">
                            <div className="text-xs font-weight-bold text-secondary text-uppercase mb-1">{this.props.title}</div>
                            <div className="h5 mb-0 font-weight-bold text-gray-800">{content.content}</div>
                        </div>
                        <div className="col-auto">
                            <div className={icon[type].btnClass} role="button">
                                {icon[type].icon}
                            </div>
                        </div>
                    </div>
                    <div className='mt-3'>
                        {content.description}
                    </div>
                </div>
            </div>
        )
    }
}