import React from "react";
import Chart from 'chart.js' 

const horizontalBarChartOption= {
    tooltips: {
        enabled: false,
        displayColors: true,
        mode: 'nearest',
        intersect: false
    },
    scales: {
        xAxes: [{
            stacked: true,
            display: false
        }],
        yAxes: [{
            stacked: true
        }]
    },
    responsive: true,
    maintainAspectRatio: false,
};

export default class HorizontalBarChart extends React.Component {

    canvasRef = React.createRef();
    componentDidUpdate() {
        // console.log(this.props)
        this.myChart.data.datasets[0].data = this.props.auditedData
        this.myChart.data.datasets[1].data = this.props.errorData
        this.myChart.data.datasets[2].data = this.props.pendingData
        this.myChart.update();
    }

    componentDidMount() {
        this.myChart = new Chart(this.canvasRef.current, {
            type: 'horizontalBar',
            data: {
                datasets: [{
                    data: this.props.auditedData,
                    backgroundColor: "#77C95F",
                    label: 'Audited'
                }, {
                    data: this.props.errorData,
                    backgroundColor: "#FF5757",
                    label: 'Error'
                }, {
                    data: this.props.pendingData,
                    backgroundColor: "#939290",
                    label: 'Not yet audited'
                }]
            },
            options: horizontalBarChartOption,
        });
    }


    render() {
        return (
            <canvas ref={this.canvasRef} />
        )
    }
}