import React from "react";
import '../css/dashboard.css';
import homeSvg from '../image/home.svg'
import deviceListSvg from '../image/deviceList.svg'
import mapSvg from '../image/map.svg'
import itm from '../image/ITM.svg';
import photoSvg from '../image/photo.svg'
import { Link } from 'react-router-dom'


export default class SideBar extends React.Component {
    render() {
        return (
            <ul className="navbar-nav sidebar bg-white accordion">
                <Link to='/' className="sidebar-brand d-flex align-items-center bg-itm justify-content-center">
                    <div className="sidebar-brand-text mx-3"><img alt="main" width="100" src={itm} /></div>
                </Link>
                <li className="nav-item active">
                    <Link to='/' className='nav-link'>
                        <img alt="main" width="20" src={homeSvg} className="mr-1" />
                        <span>Home</span>
                    </Link>
                </li>
                <li className="nav-item active">
                    <Link to='/deviceList' className='nav-link'>
                        <img alt="deviceList" width="20" className="mr-1" src={deviceListSvg} />
                        <span>Device List</span>
                    </Link>
                </li>

                <li className="nav-item active">
                    <Link to="/location" onClick={() => window.location.href = '/location'} className="nav-link" >
                        <img alt="location" width="20" className="mr-1" src={mapSvg} />
                        <span>Map</span>
                    </Link>
                </li>

                <li className="nav-item active">
                    <Link to="/photos" onClick={() => window.location.href = '/photos'} className="nav-link">
                        <img alt="photos" width="20" className="mr-1" src={photoSvg} />
                        <span>Photos</span>
                    </Link>
                </li>

            </ul>

        )
    }
}