import React from "react";
import '../../css/dashboard.css';
import HorizontalBarChart from "../chart/horizontalBarChart"
import * as categoryConstants from '../../common/DeviceCategory';
import menuSvg from '../../image/menu.svg'
import {Link} from 'react-router-dom'
import {timeStamp2String} from '../util'
import DeviceProfile from "../DeviceProfile/DeviceProfile"
import Modal from "react-bootstrap/Modal";

class DeviceCard extends React.Component {
    state = {showProfile: false};

    setShow = (show) => {
        this.setState({showProfile: show})
    }
    handleClose = () => this.setShow(false);
    handleShow = () => this.setShow(true);

    onProfileCancelClicked = () => {
        // this.props.onProfileCancelClicked();
        this.handleClose();
    }

    onProfileSaveClicked = (device) => {
        // console.log("in profile", device)
        this.props.onProfileSaveClicked(device);
        this.handleClose();
    }
    onEditProfileClicked = () => {
        this.handleShow();
    }

    renderStatusBarChart() {
        // console.log("barchart data", this.props.horizontalBarChartData);
        if(!this.props.horizontalBarChartData){
            // console.log("not loading yet")
            return null
        }
        function IsBarChartDataEmpty({auditedData, errorData, pendingData}) {
            return (auditedData[0] === 0 && pendingData[0] === 0 && errorData[0] === 0)
        }

        if (IsBarChartDataEmpty(this.props.horizontalBarChartData)) {
            return (
                <div className="card-title p-3">
                    <h6>Status (Last 12 Hours)</h6>
                    <div className="chart-horizontalBar">
                        No data available
                    </div>
                </div>
            )
        }
        return (
            <div className="card-title p-3">
                <h6>Status (Last 12 Hours)</h6>
                <div className="chart-horizontalBar">
                    <HorizontalBarChart {...this.props.horizontalBarChartData} />
                </div>
            </div>
        );
    }

    render() {
        const updateTime = timeStamp2String(this.props.device.updateTime)
        const toPath = `/deviceData/${this.props.device.id}`
        // console.log("DeviceCard render", this.props.device);
        return (
            <div className="bg-white card">
                <div className="card-body border-bottom border-dark">
                    <div className="row">
                        <div className="col-xl-4 col-lg-4 align-self-center text-center">
                            <Link to={toPath} className='btn btn-dark btn-xl btn-circle text-light'>
                                <img alt="" src={categoryConstants[`ICON_${this.props.device.category}`]} width="40" className="categoryIcon"/>
                            </Link>
                        </div>
                        <div className="col-xl-8 col-lg-8">
                            <div className="col-12 col-lg-12">
                                <div className="row">
                                    <div className="col-xl-9 col-lg-9 mt-2">
                                        <h5 className="card-title text-left">
                                            <Link to={toPath}>{this.props.device.name}</Link>
                                        </h5>
                                    </div>
                                    <div className="col-xl-3 col-lg-3 text-right">
                                        <button className="btn" type="button" id="dropdownMenuButton"
                                                data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                            <img src={menuSvg} width="20" alt=""/>
                                        </button>
                                        <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                            <button type="button" className="btn btn-white"
                                                    onClick={this.onEditProfileClicked}>
                                                Edit Profile
                                            </button>
                                        </div>
                                        <Modal show={this.state.showProfile} onHide={this.handleClose}
                                               backdrop="static"
                                               aria-labelledby="contained-modal-title-vcenter"
                                               centered>
                                            <Modal.Body className="bg-itm">
                                                <DeviceProfile device={this.props.device}
                                                               onCancelClicked={this.onProfileCancelClicked}
                                                               onSaveClicked={this.onProfileSaveClicked}/>
                                            </Modal.Body>
                                        </Modal>
                                    </div>
                                </div>
                                <p className="text-dark card-text small">{this.props.device.hardwareType}</p>
                                <p><small>Last Update Time</small></p>
                                <p><small>{updateTime}</small></p>
                            </div>
                        </div>
                    </div>
                </div>
                {this.renderStatusBarChart()}
            </div>

        )
    }


}

export default DeviceCard;
